<template>
  <v-container style="height: 100%;width:100%;">
    <v-card height="100%" flat v-show="false">
      <v-toolbar flat>
        <v-toolbar-title>可用Packages</v-toolbar-title>
      </v-toolbar>
      <v-tabs vertical height="100%" show-arrows>
        <v-tab v-for="d in metadata" :key="d.name" @click="show(d)">
          <v-icon left> mdi-cube </v-icon>
          {{ d.name }}
        </v-tab>

        <v-tab-item v-for="d in metadata" :key="d.name">
          <v-card flat height="100%" v-if="selectedItem">
            <v-card-text>
              <v-card-title class="text-h4">
                {{ d.name }}
              </v-card-title>
              <v-card-subtitle>
                <span>
                  {{ selectedItem.description }}
                </span>
                <br />
                <span> 版本:{{ selectedItem.version }} </span>
              </v-card-subtitle>
              <v-card-text>
                <h3>下载:</h3>
                <v-card outlined>
                  <v-card-text>
                    <v-icon>mdi-download</v-icon>
                    <a :href="selectedItem.file" target="_blank">{{
                        selectedItem.file
                    }}</a>
                  </v-card-text>
                </v-card>
                <h3>CLI:</h3>

                <v-card outlined>
                  <v-card-text>
                    {{ toCommand(selectedItem.name) }}
                  </v-card-text>
                </v-card>
              </v-card-text>
            </v-card-text>
          </v-card>
        </v-tab-item>
      </v-tabs>
    </v-card>

    <v-navigation-drawer width="400px" v-model="drawer" absolute right temporary>
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title>索引</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list dense>
        <v-list-item @click="show(item)" v-for="item in metadata" :key="item.name" link>
          <v-list-item-icon>
            <v-icon>mdi-cube</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ item.name }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <v-card height="100%" flat>
      <v-card-actions>

        <v-btn @click="drawer = !drawer" outlined color="primary">展开列表</v-btn>
      </v-card-actions>

      <v-card flat height="100%" v-if="selectedItem">
        <v-card-text>
          <v-card-title class="text-h4">
            {{ selectedItem.name }}
          </v-card-title>
          <v-card-subtitle>
            <span>
              {{ selectedItem.description }}
            </span>
            <br />
            <span> 版本:{{ selectedItem.version }} </span>
          </v-card-subtitle>
          <v-card-text>
            <h3>下载:</h3>
            <v-card outlined>
              <v-card-text>
                <v-icon>mdi-github</v-icon>
                <a :href="toGitHub(selectedItem.repo)" target="_blank">{{
                    selectedItem.repo
                }}</a>
                <br />

                <v-icon>mdi-download</v-icon>
                <a :href="selectedItem.file" target="_blank">{{
                    selectedItem.file
                }}</a>
              </v-card-text>
            </v-card>
            <h3>CLI:</h3>

            <v-card outlined>
              <v-card-text>
                <v-btn icon @click="copy(toCommand(selectedItem.name))">
                  <v-icon>mdi-clipboard-text-outline</v-icon>
                </v-btn>
                {{ toCommand(selectedItem.name) }}
              </v-card-text>
            </v-card>

            <h3>README:</h3>

            <v-card outlined>
              <v-card-text>
                <div v-html="readme" />
              </v-card-text>
            </v-card>
          </v-card-text>
        </v-card-text>
      </v-card>
    </v-card>
  </v-container>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    metadata: null,
    selectedItem: null,
    drawer: null
  }),
  mounted() {
    axios.get("metadata/zh/gc-packages/index.json").then((res) => {
      this.metadata = res.data;
      console.log(res.data);
      this.show(res.data[0]);

    });
  },
  methods: {
    show(d) {
      this.selectedItem = d;
    },
    toGitHub(g) {
      return `https://github.com/${g}`;
    },
    toCommand(n) {
      return `gc-cli addpkg ${n}`;
    },
    copy(t) {
      if (navigator.clipboard) {
        navigator.clipboard.writeText(t);
      }
    },
  },
};
</script>

<style>
.card {
  height: 100% !important;
}

.v-tab {
  justify-content: flex-start !important;
}
</style>